html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  align-items: center;
  background: linear-gradient(180deg, #E6E7EB 0%, #A1A4B1 100%), #fff;
  display: flex;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  font-style: normal;
  height: 100vh;
  justify-content: center;
  width: 100vw; }

.editor {
  margin: 16px; }
  .editor__area {
    background: #fff;
    border: 1px solid #A1A4B1;
    border-radius: 4px;
    color: #A1A4B1;
    font-weight: normal;
    line-height: 24px;
    min-height: 280px;
    max-width: 448px;
    padding: 16px;
    width: 100%; }
  .editor__button {
    border: 1px solid #121232;
    border-radius: 4px;
    font-weight: 600;
    letter-spacing: 0.5px;
    line-height: 16px;
    padding: 16px 32px; }
    .editor__button--save {
      background: #121232;
      color: #fff;
      margin-left: 8px; }
    .editor__button--load {
      background: #fff;
      color: #121232;
      margin-right: 8px; }
  .editor__buttons {
    display: flex;
    height: 100%;
    justify-content: flex-end;
    margin-top: 24px;
    width: 100%; }
  .editor__title {
    color: #000000;
    font-size: 44px;
    font-weight: 600;
    line-height: 64px; }
    @media screen and (max-width: 425px) {
      .editor__title {
        font-size: 22px; } }
  @media screen and (max-width: 425px) {
    .editor {
      width: 100%; } }
